@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
  font-family: "Nunito", sans-serif;
}

.logo_container {
  text-align: center;
  margin: 40px auto 0px;
}

.jumbotron_container {
  width: 342px;
  height: 66px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 40px auto 30px;
}

.left_bar {
  width: 7px;
  height: 66px;
  background: #4592af;
  border-radius: 29px;
}

.jumbotron_text {
  width: 320px;
  height: 50px;
  font-weight: normal;
  font-size: 18px;
  line-height: 25px;
  color: #000000;
}

.jumbotron_link_text {
  color: #87c3d9;
}

.button_container {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #4592af;
  width: 140px;
  height: 50px;
  border-radius: 10px;
}

.button_text {
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 37px;
  color: white;
}

.button_container :hover {
  cursor: pointer;
}

.main_page_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.welcome_text {
  font-weight: 800;
  font-size: 29px;
  line-height: 40px;
  margin-top: 10px;
}

.bottom_text {
  margin: 40px auto 10px;
  color: #3f768a;
}

.input_container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.input {
  border: none;
  outline: none;
  margin-top: 60px;
}
.input_bottom_bar {
  width: 200px;
  height: 7px;
  background: #4592af;
  border-radius: 11px;
  margin-bottom: 30px;
}

.main_container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.heading {
  font-weight: 800;
  font-size: 29px;
  line-height: 40px;
  margin-top: 10px;
  text-align: center;
  margin-bottom: 10px;
}

.item_container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 411px;
  height: 81px;
  background: #a9cbd8;
  box-shadow: 0px 4px 9px rgba(0, 0, 0, 0.25);
  border-radius: 26px;
  margin: 20px auto 0px;
}

.item_detail_container {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.item_detail {
  font-style: normal;
  font-weight: 300;
  font-size: 21px;
  line-height: 29px;
}

.add_item_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin: 40px 10px auto;
}

